<template>
	<view class="container">
		<u-no-network></u-no-network>
		<view class="box"  v-for="(item,index) in boxList" :key="index">
			<view class="shang display_flex flex_between">
				<view class="shangconteny display_flex flex_start">
					<image src="/static/img/usdt.png" mode=""></image>
					<text>{{item.id}}</text>
				</view>
				<view class="jiayi font_size_26" @click="navgopages(item.id,1)">
					交易记录
				</view>
			</view>
			<!-- FF775F  FF5050 -->
			<view class="xia display_flex flex_between">
				<view class="zongshu font_size_28" >
					<u-count-to color="#FF2626" :start-val="30" :end-val="item.yue" :decimals="2"></u-count-to>
					{{item.id}}
				</view>
				<view class="display_flex flex_start">
					<view class="chongzhi" v-if="item.id =='USDT'" @click="navgopages(item.id,2)">充值</view>
					<view class="tixian" @click="navgopages(item.id,3)">提现</view>
				</view>
			</view>
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				boxList:[
					{icon:'',id:"USDT",yue:'1521.00'},
					{icon:'',id:"DOGZ",yue:'1522.00'},
					{icon:'',id:"SHIB",yue:'1523.00'},
					{icon:'',id:"AKITA",yue:'1524.00'},
				],
			}
		},
		methods: {
			navgopages(id,type){
				if(type == 1){
					uni.navigateTo({
						url:"./jiaoyi/jiaoyi?id="+id
					})
				}else if(type == 2){
					uni.navigateTo({
						url:"./chongzhi/chongzhi?id="+id
					})
				}else{
					uni.navigateTo({
						url:"./tixian/tixian?id="+id
					})
				}
				
			},
		}
	}
</script>

<style scoped>
	.chongzhi{
		/* FF775F  FF5050  */
		background: linear-gradient(10deg,#FF775F 0%,#FF5050 100%);
		border-radius: 14upx;
		color: #ffffff;
		font-size: 30upx;
		font-weight: 500;
		text-align: center;
		box-shadow: 5upx 5upx 10upx #FF775F;
		margin-right: 20upx;
		padding: 0 30upx;
	}
	.tixian{
		background: linear-gradient(10deg,#6BCF00 0%,#73DE00 100%);
		border-radius: 14upx;
		color: #ffffff;
		font-size: 30upx;
		font-weight: 500;
		text-align: center;
		box-shadow: 5upx 5upx 10upx #73DE00;
		padding: 0 30upx;
	}
	.zongshu{
		color: #FF2626;
		font-weight: 600;
	}
	.box{
		width: 92%;
		background: #FFFFFF;
		border-radius: 12px;
		padding: 20upx 30upx;
		margin: 40upx auto 0;
	}
	.box view{
		line-height: 60upx;
	}
	.biaoti{
		font-weight: 600;
	}
	.shang{
		border-bottom: 2upx solid #EDEDED;
		padding-bottom: 20upx;
	}
	.shangconteny{
		line-height: 50upx;
	}
	
	.shangconteny image{
		width: 50upx;
		height: 50upx;
		line-height: 50upx;
		margin-right: 20upx;
		
	}
	.jiayi{
		color: #FF2626;
	}
	.xia{
		padding-top: 30upx;
	}
</style>
